<template>
  <div class="winDemo">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>    
        <div class="line"></div>    
        <div class="iframeBox">
          <iframe src="#/winDemoExample"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">窗口综合示例 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import sliderTab from "cy_static/sliderTab";
    import sliderWin from "cy_static/sliderWin";
    import sliderScroll from "cy_static/sliderScroll";
&lt;/script&gt;
</xmp >
            
      </div>

      <h2 class="cy_h2">css 代码演示</h2>
      <div class="codeModule">     
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;style type="text/css"&gt;
    #myMenu{position:relative;overflow:hidden;background-color:#fff;border-bottom: 0.5px solid #eff1f4;cursor: e-resize;}
    #myMenu .content{display:table;white-space:nowrap}
    #myMenu .content .item{cursor: e-resize;padding:15px 15px;display:inline-block;color:#6f6f6f;}
    #myMenu .content .item.curr{font-weight:bold;position:relative;color:#000}
    #myMenu .progress{transition:width 100ms;padding:0 15px;position:absolute;min-width:10px;height:2px;bottom:6px}
    #myMenu .progress:after{content:" ";background:#ee0a24;display:block;height:100%;width: 86%;margin: 0 auto;}
    #myMenuList{position:absolute;left:0;right:0;top:47.5px;bottom:0;overflow: hidden;}
    #myMenuList .mWin{background-color:#fff;padding: 8px;min-height: 100%;}
&lt;/style&gt;
</xmp >  
      </div>
      

      <h2 class="cy_h2">HTML代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle3" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal3" style="display:none">
&lt;div id="myMenu"&gt;
    &lt;div class="progress"&gt;&lt;/div&gt;
    &lt;div class="content"&gt;
        &lt;span class="item"&gt;陆游(宋)&lt;/span&gt;
        &lt;span class="item"&gt;杜甫(唐)&lt;/span&gt;
        &lt;span class="item"&gt;崔颢(唐)&lt;/span&gt;
        &lt;span class="item"&gt;白居易(唐)&lt;/span&gt;
        &lt;span class="item"&gt;刘禹锡(唐)&lt;/span&gt;
        &lt;span class="item"&gt;王安石(北宋)&lt;/span&gt;
        &lt;span class="item"&gt;王之涣(唐)&lt;/span&gt;
        &lt;span class="item"&gt;龚自珍(清朝)&lt;/span&gt;
    &lt;/div&gt;
&lt;/div&gt;
&lt;div id="myMenuList"&gt;
    &lt;div class="mWin"&gt;
        &lt;p&gt;陆游《游山西村》&lt;/p&gt;
        &lt;p&gt;莫笑农家腊酒浑，丰年留客足鸡豚。&lt;/p&gt;
        &lt;p&gt;山重水复疑无路，柳暗花明又一村。&lt;/p&gt;
    &lt;/div&gt;
    &lt;div class="mWin"&gt;
        &lt;p&gt;杜甫《蜀相》&lt;/p&gt;
        &lt;p&gt;丞相祠堂何处寻？锦官城外柏森森。&lt;/p&gt;
        &lt;p&gt;映阶碧草自春色，隔叶黄鹂空好音。&lt;/p&gt;
    &lt;/div&gt;
    &lt;div class="mWin"&gt;
        &lt;p&gt;崔颢《黄鹤楼》&lt;/p&gt;
        &lt;p&gt;昔人已乘黄鹤去，此地空余黄鹤楼。&lt;/p&gt;
        &lt;p&gt;黄鹤一去不复返，白云千载空悠悠。&lt;/p&gt;
    &lt;/div&gt;
    &lt;div class="mWin"&gt;
        &lt;p&gt;白居易《大林寺桃花》&lt;/p&gt;
        &lt;p&gt;人间四月芳菲尽，山寺桃花始盛开。&lt;/p&gt;
        &lt;p&gt;长恨春归无觅处，不知转入此中来。&lt;/p&gt;
    &lt;/div&gt;
    &lt;div class="mWin"&gt;
        &lt;p&gt;刘禹锡《秋风引》&lt;/p&gt;
        &lt;p&gt;何处秋风至？萧萧送雁群。&lt;/p&gt;
        &lt;p&gt;朝来入庭树，孤客最先闻。&lt;/p&gt;
    &lt;/div&gt;
    &lt;div class="mWin"&gt;
        &lt;p&gt;王安石《泊船瓜洲》&lt;/p&gt;
        &lt;p&gt;京口瓜洲一水间，钟山只隔数重山。&lt;/p&gt;
        &lt;p&gt;春风又绿江南岸，明月何时照我还？&lt;/p&gt;
    &lt;/div&gt;
    &lt;div class="mWin"&gt;
        &lt;p&gt;王之涣《登鹳雀楼》&lt;/p&gt;
        &lt;p&gt;白日依山尽，黄河入海流。&lt;/p&gt;
        &lt;p&gt;欲穷千里目，更上一层楼。&lt;/p&gt;
    &lt;/div&gt;
    &lt;div class="mWin"&gt;
        &lt;p&gt;龚自珍《己亥杂诗》&lt;/p&gt;
        &lt;p&gt;浩荡离愁白日斜，吟鞭东指即天涯。&lt;/p&gt;
        &lt;p&gt;落红不是无情物，化作春泥更护花。&lt;/p&gt;
    &lt;/div&gt;
&lt;/div&gt;
</xmp >
      </div>

      <h2 class="cy_h2">javascript 代码演示</h2>
      <div class="codeModule">
          <div class="codeStyle">
<codemirror ref="codeStyle4" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal4" style="display:none">
&lt;script type="text/javascript"&gt;
    var doc = document, m = this;
    var myMenu = doc.querySelector("#myMenu"); 
    var tabItems = myMenu.querySelectorAll(".item");  
    var progress = myMenu.querySelector(".progress");
    var myMenuList = doc.querySelector("#myMenuList");
    var winDom = myMenuList.querySelectorAll(".mWin");
    var myTab, myWin;   
    // 设置进度
    var setProgress = function(index, left) {
            var _left = 0;
            for (var i = 0; i < index; i++) {
                _left += tabItems[i].offsetWidth;
            }
            _left += (left - index) * tabItems[index].offsetWidth;
            myTab.setCss(Math.round(left));
            Object.assign(progress.style, {
                left: _left + "px",
                width: tabItems[index].offsetWidth + "px"
            });
    };
    //设置TAB滚动效果
    myTab = sliderTab({
            elem: myMenu,
            content: ".content",
            onScroll: function(left) {
                progress.style.marginLeft = left + "px";
            },
            onClick: function(index) {
                if (myWin) {
                    myWin.setCurrIndex(index);
                    // 加载相关数据code;
                }
            }
    });
    //窗口左右切换#要设置高度
    myWin = sliderWin({ 
            currIndex: 0,
            elem: myMenuList,
            content: winDom,
            onScroll: function(index, dom, left) {
                setProgress(index, left);
                // 加载相关数据code;
            },
            onChange: function(index, dom) {
                myTab.setCurrIndex(index);
                // 加载相关数据code;
            }
    });
    //窗口上下切换#要设置高度
    winDom.forEach(function (item, index) {
        var winScroll = new sliderScroll({// 绑定滚动窗口
            elem: myMenuList,   
            content: item,
            isTopEffect:true,    
            isLockfull: true,             
            pullDown:{// 顶部加载
                callback :function (params, content) {
                    setTimeout(function(){// 模拟接口返回
                        winScroll.isEndPull(false);
                    },13);
                }
            },// 底部加载
            pushDown:{
                callback : function () {
                    setTimeout(function(){// 模拟接口返回
                        winScroll.isEndPush(true);
                    },13);
                }
            }
        });
    });
&lt;/script&gt;
</xmp >
          </div>  
      </div>

       
     




    </div> 
  </div>
</template>

<script>
export default {
  name: 'winDemo',
  data () {
    return {
      msg: 'winDemo',
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  }, 
  mounted(){
    var m = this;
    m.$refs.codeStyle1.codemirror.setValue(m.$refs.codeStyleVal1.innerHTML.replace(/^\s|\s+$/g,''));
    m.$refs.codeStyle2.codemirror.setValue(m.$refs.codeStyleVal2.innerHTML.replace(/^\s|\s+$/g,''));
    m.$refs.codeStyle3.codemirror.setValue(m.$refs.codeStyleVal3.innerHTML.replace(/^\s|\s+$/g,''));
    m.$refs.codeStyle4.codemirror.setValue(m.$refs.codeStyleVal4.innerHTML.replace(/^\s|\s+$/g,''));
  }
}
</script>  
<style scoped>
  .cy_example .iframeBox{position:absolute;top:48px;width:100%;bottom:0}
</style>



